@use "colors";
@use "cams-variables";

:host {
  ::ng-deep {
    .mill-drag-and-drop {
      .mill-attached-file {
        display: none;
      }
    }
  }
}

.application-details-document-list-file-upload {
  display: flex;
  height: 100%;
  justify-content: center;
  align-items: center;

  &__wrapper {
    display: flex;
    flex-direction: column;

    .uploader-errors {
      display: flex;
      align-items: center;
      color: colors.$orange-50;
      font-size: cams-variables.$text-size-default;
      margin-block-start: 5px;

      .icon-wrapper {
        height: 20px;
        align-items: center;
        display: flex;
      }

      .texts-wrapper {
        display: flex;
        justify-content: center;
        align-items: flex-start;
        flex-direction: column;
        max-width: calc(100% - 12px);

        &__text {
          padding-left: 8px;
        }
      }
    }
  }

  &__files {
    display: flex;
    gap: 15px;

    &-attached {
      display: flex;
      flex-direction: column;
      gap: 12px;
      width: 300px;
    }
  }
}
